<template>
  <h1 class="title">Space News</h1>
  <NewsPage :image="image" :description="description" :news="news" />
</template>

<script>

import NewsPage from "./components/NewsPage.vue"

export default {
  name: 'App',
  components: {
    NewsPage
  }, data() {
    return {
      image: "https://images.cryptocompare.com/news/default/thenewscrypto.png",
      description: "As of July 2022, the deck claims that the company lost 8k BTC and 56k ETH in June. The wallet",
      title: "",
      news: null,
      hey: this.getNews()


    }
  },
  methods: {
    async getNews() {
      const apiUrl = "https://api.spaceflightnewsapi.net/v3/articles?_limit=100"
      try {

        const response = await fetch(apiUrl);
        this.news = await response.json();
        console.log(this.news)
        this.title = this.news.Data[0].title
        this.image = this.news.Data[0].imageurl
        this.description = this.news.Data[0].body
        console.log(this.title)


      } catch (error) {

        console.log(error)
      }
    }

  }
}
</script>

